body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #d9e1f4;
}

.clock {
  width: 31.25rem;
  height: 31.25rem;
  border-radius: 15.625rem;
  border: 5px solid #7087c7;
  background-color: #7bdac0;
  position: absolute;
}

#seconds {
  width: 0.125rem;
  background-color: #32594e;
  height: 15rem;
  position: absolute;
  right: 15.5625rem;
  bottom: 15.375rem;
  transform-origin: 0 100%;
}

#minutes {
  width: 0.125rem;
  background-color: #564982;
  height: 14.0625rem;
  position: absolute;
  right: 15.5625rem;
  bottom: 15.375rem;
  transform-origin: 0 100%;
}

#hours {
  width: 0.25rem;
  background-color: #425177;
  height: 12.5rem;
  position: absolute;
  right: 15.5625rem;
  bottom: 15.375rem;
  transform-origin: 0 100%;
}

#center {
  position: absolute;
  width: 1.5625rem;
  height: 1.5625rem;
  border-radius: 10px;
  right: 240px;
  bottom: 235px;
  background-color: #564982;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}